<style scoped lang="less">
</style>
<template>
  <div class="addConsult content-layout">
    <div class="content-header">
      <div class="content-btn">
        <el-button type="info" size="small" @click="addConsult">新建会诊</el-button>
      </div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="病理号"></el-table-column>
        <el-table-column prop="b" label="姓名"></el-table-column>
        <el-table-column prop="c" label="性别"></el-table-column>
        <el-table-column prop="d" label="年龄"></el-table-column>
        <el-table-column prop="e" label="送检部位"></el-table-column>
        <el-table-column prop="f" label="临床诊断"></el-table-column>
        <el-table-column prop="g" label="绑定切片"></el-table-column>
        <el-table-column prop="h" label="流程状态" width="150"></el-table-column>
        <el-table-column label="操作" width="200">
          <template scope="scope">
            <el-button type="text" size="small" @click.native.prevent="editconsult(scope.$index, tableData)">编辑</el-button>
            <el-button type="text" size="small" @click.native.prevent="consultRecordSubmit(scope.$index, tableData)">提交</el-button>
            <el-button type="text" size="small" @click.native.prevent="consultRemove(scope.$index, tableData)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <addedit-record v-show="showRecord" :recordData="recordData"  @getChildMessage="closeRecord"></addedit-record>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import addeditRecord from '../../../public/addeditRecord'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        seachData:  {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          xbval: searchDataModule.xbval,
          xb: searchDataModule.xb,
          seachInfo: searchDataModule.seachInfo
        },
        tableData: [
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '感冒', g: '否', h: '待分配'}
        ],
        currentPage: 1,
        recordData: {},
        showRecord: false,//病历信息显示隐藏
      }
    },
    props: ['a'],
    components: {
      headerSearch,
      addeditRecord
    },
    mounted(){
      searchInitDefault(searchDataModule);
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      seachFn(val){
        console.log(this.seachData)
      },
      /*新建会诊-------新建会诊传入的参数的值为空。编辑会诊传入的参数的值为当前病历的信息*/
      addConsult(){
        this.showRecord = true;
        this.recordData = {
          radio: '会诊病历'
        }
      },
      editconsult(i,arr){
          /*编辑会诊病历*/
        this.showRecord = true;
        this.recordData = {
          radio: '2'
        }
      },
      closeRecord(val) {
        //evtValue 关闭  是子组件传过来的值关闭病历信息组件
        this.showRecord = val
      },
      /*提交会诊病历*/
      consultRecordSubmit(i,arr){
        this.$confirm('此操作将提交病历, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
//          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '提交成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消提交'
          });
        });
      },
      /*删除会诊病历*/
      consultRemove(i,arr){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    }
  }
</script>



