module.exports = function (obj) {
  obj.starttime = '';//开始时间
  obj.endtime = '';//结束时间
  obj.ztval = obj.zt[0].value;//状态
  obj.flval = obj.fl[0].value;//分类
  obj.zjval = obj.zj[0].value;//专家
  obj.seachInfo = '';//自定义搜索
  obj.xbval = obj.xb[0].value;//性别
  obj.mkval = obj.mk[0].value;//模块
  obj.ysval = obj.ys[0].value;//医生名
  obj.yyval = obj.yy[0].value;//医院名称
  obj.qyval = obj.qy[0].value;//区域
  obj.lxval = obj.lx[0].value;//病历类型
}
